    max-width:640px
  }
}

@media (min-width: 768px){
  .experience-wrapper{
    max-width:768px
  }
}

@media (min-width: 1024px){
  .experience-wrapper{
    max-width:1024px
  }
}

@media (min-width: 1280px){
  .experience-wrapper{
    max-width:1280px
  }
}

@media (min-width: 1536px){
  .experience-wrapper{
    max-width:1536px
  }
}

.experience-wrapper{
  padding-left:1rem;
  padding-right:1rem
}

@media (min-width: 1024px){
  .experience-wrapper{
    margin-left:auto;
    margin-right:auto;
    padding-left:0px;
    padding-right:0px
  }
}

.experience-wrapper h1{
  color:#00a09d;
  font-weight:600;
  font-size:1.5rem;
  line-height:2rem;
  text-align:center;
  text-transform:uppercase;
  text-decoration:underline
}

@media (min-width: 1024px){
  .experience-wrapper h1{
    font-size:2.25rem;
    line-height:2.5rem;
    text-align:left
  }
}

.experience-wrapper .intern{
  line-height:2.5rem;
  margin-top:1.5rem;
  margin-bottom:1.5rem
}

.experience-wrapper .intern h4{
  font-weight:600;
  font-size:1.125rem;
  line-height:1.75rem;
  letter-spacing:0.1em
}

@media (min-width: 1024px){
  .experience-wrapper .intern h4{
    font-size:1.5rem;
    line-height:2rem
  }
}

.experience-wrapper .intern div h5{
  background:rgba(1,200,199,.3);
  display:inline-block;
  font-size:0.875rem;
  line-height:1.25rem;
  padding-top:0.25rem;
  padding-bottom:0.25rem;
  padding-left:0.5rem;
  padding-right:0.5rem
}

@media (min-width: 1024px){
  .experience-wrapper .intern div h5{
    font-size:1rem;
    line-height:1.5rem
  }
}

.experience-wrapper .intern p{
  font-weight:500;
  font-size:0.75rem;
  line-height:1rem
}

@media (min-width: 1024px){
  .experience-wrapper .intern p{
    font-size:0.875rem;
    line-height:1.25rem
  }
}

.experience-wrapper .intern ul{
  font-weight:300;
  font-size:1rem;
  line-height:1.5rem;
  margin-top:1rem;
  margin-bottom:1rem;
  margin-left:0.5rem
}

@media (min-width: 1024px){
  .experience-wrapper .intern ul{
    font-size:1.25rem;
    line-height:1.75rem;
    margin-top:0px;
    margin-bottom:0px;
    margin-left:0px
  }
}

.experience-wrapper .intern ul li{
  line-height:1.5;
  list-style-position:outside;
  list-style-type:disc
}

@media (min-width: 1024px){
  .experience-wrapper .intern ul li{
    line-height:2.5rem
  }
}
.education-wrapper{
  width:100%
}

@media (min-width: 640px){
  .education-wrapper{
    max-width:640px
  }
}

@media (min-width: 768px){
  .education-wrapper{
    max-width:768px
  }
}

@media (min-width: 1024px){
  .education-wrapper{
    max-width:1024px
  }
}

@media (min-width: 1280px){
  .education-wrapper{
    max-width:1280px
  }
}

@media (min-width: 1536px){
  .education-wrapper{
    max-width:1536px
  }
}

.education-wrapper{
  display:flex;
  flex-direction:column;
  justify-content:center;
  margin-left:auto;
  margin-right:auto;
  margin-top:2rem;
  padding-left:1rem;
  padding-right:1rem
}

@media (min-width: 1024px){
  .education-wrapper{
    padding-left:0px;
    padding-right:0px
  }
}

.education-wrapper .wrapper{
  display:flex;
  justify-content:center
}

.education-wrapper .wrapper .inner-wrapper{
  background:rgba(1,200,199,.3);
  padding:0.5rem;
  text-align:center;
  width:100%
}

@media (min-width: 1024px){
  .education-wrapper .wrapper .inner-wrapper{
    width:66.666667%
  }
}

.education-wrapper .wrapper .inner-wrapper h1{
  font-weight:600;
  font-size:0.875rem;
  line-height:1.25rem
}

@media (min-width: 1024px){
  .education-wrapper .wrapper .inner-wrapper h1{
    font-size:1.5rem;
    line-height:2rem
  }
}

@media (min-width: 1024px){
  .education-wrapper .wrapper .inner-wrapper h1:not(:first-child){
    letter-spacing:0.1em
  }
}

.education-wrapper .wrapper .inner-wrapper h6{
  font-size:0.875rem;
  line-height:1.25rem
}

@media (min-width: 1024px){
  .education-wrapper .wrapper .inner-wrapper h6{
    font-size:1rem;
    line-height:1.5rem;
    letter-spacing:0.1em
  }
}

.education-wrapper .wrapper .inner-wrapper p{
  font-size:1rem;
  line-height:1.5rem;
  --tw-text-opacity:1;
  color:rgba(55, 65, 81, var(--tw-text-opacity))
}

.dark .education-wrapper .wrapper .inner-wrapper p{
  --tw-text-opacity:1;
  color:rgba(156, 163, 175, var(--tw-text-opacity))
}

@media (min-width: 1024px){
  .education-wrapper .wrapper .inner-wrapper p{
    font-size:1.125rem;
    line-height:1.75rem
  }
}

.education-wrapper .down-arrow{
  color:#00a09d;
  display:flex;
  justify-content:center
}
